// () data.js Module   js data object  (  )  js       import    
import data from './data.js';
// import Detail from './pages/Detail.js';
// import Cart from './pages/Cart.js'

// lazy import = lazy( () => import(' ') ) 
//  : (react   1) aplication   component ,  html, js    react  ,         ..
