// () Detail Cart  react aplication    html, js   ,   import   ,      Module    
//  ->  : Detail Cart   ,   
const Detail = lazy( () => import('./pages/Detail.js') )
const Cart = lazy( () => import('./pages/Cart.js') )

// () react-bootstrap    css import
import 'bootstrap/dist/css/bootstrap.min.css';

// () react-bootstrap    component  ,  component  {} import  jsx  
import { Container, Row, Col, Navbar, Nav}  from 'react-bootstrap';

// () js import       alias( )   
//  -> (!) ! react  js image   import  , src       
import img from './img/image2.jpg';

// () react-router-dom   , Routes, Route, Link component   
//  -> Routes
//      :  component     url   path    element    Route component navigation    container  component

//  -> Route
//      :          component 
//        (path : url   , element :     component )

//  -> Link  
//      : react-router-dom   a    component
//        (to :  Link component  ,   Route component url  )         
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom';

// () ajax     axios   , axios object    
import axios from 'axios'

// react  ajax           react-query   import  
import { useQuery } from '@tanstack/react-query' 

// () public   
//  :  , bulid    , src        public     
//    (=     , txt, json    static  , public   )

//   # () public    src   '/.'   
//      -> !  URL 'www.lsh.com//~'          
//          ->  react   public     {process.env.PUBLIC_URL} 
//              (= process.env  Node.js      ,            ,  PUBLIC_URL   act     public URL  )

//              ex) <img src="/logo192.png" width="80%" />
//                    -> 'www.lsh.com/goods'    image rendering  ..  
//                         -> goods   <img src="/goods/logo192.png" width="80%" /> 

// () Context API
//   : props              react  API
//      ->   state  context   ,  context  component  state  ,     state  

//   # 
//      1) createContext() context 
//      2)  context  component <context.Provider>  
//      3) <context.Provider>  value  ,  state js {}    
//      4)  context export ,  component   context   import
//      5) useContext(context) reacthooks  ,  context1 state  state    
//      6) 5 .. props    component  context   state static     

//         ex) export let Context1 = createContext();

//             <Context1.Provider value={{ stock, shoes }}>
//                <Detail shoes = {shoes} /> 
//             </Context1.Provider>

//             ------------( component  jsx  ...)-----------
//             import { Context1 } from '';

//             ------------( Detail  compoent..)--------------
//             let example = useContext(Context1);
//               -> example.state[index]  

//   # () Context API   
//       -> (!)  :  2  ...    state ,       'redux'  

//       1) Context   component    rerendering 
//          :  context state  component  context   component    ...
//             -> context state   ,   component  rerendering ...
//                (=  rendering  ,    ,     )

//       2) Context   component  
//          :  component context  ,   context, useContext   ? 
//              ->  API  component        ?
//                  ->   props    ,       


// createContext 
//  : state ,   component     context(= context API )  
//    (= state      context ,      )
export let Context1 = createContext();

function App() {

  // state shoes data.js     object    data  
  let [shoes, setShoes] = useState(data);
  
  // () Context1   context     component    state
  //   ->  context1.provider  detail    state  
  let [stock, setStock]= useState([10, 11, 12]);

  // useNavigate 
  //  : Link component ,   react hooks  react router  react hooks  
  //     - useNavigate('url')
  //        : <Link to = "url" />  

  //     - useNavigate(+ or -)
  //        : +    , -     
  let navigate = useNavigate();

  // ()       3   
  
  // ()  , url json  state moreView,   3       state extraShoes
  let [moreView, setMoreview] = useState(2);
  let [extraShoes, setExtraShoes] = useState(null);

  // ()   5mb     localStorage & sessionStorage array/object   
  let obj = {name : 'kim'};



  // localStorage & sessionStorage
  //  :       
  //    (=   client         )
  //        -> state localStorage      
  //            ex) redux-persist : redux  state  localstorage    

  //   # localStorage & sessionStorage  
  //      1)   
  //      2) key, value  
  //      3)   (=     ..)

  //   # localStorage & sessionStorage 
  //      1)  
  //          - localStorage   :    
  //          - sessionStorage :       (=    )

  //      2)  
  //          - localStorage   :  ()      (=     )
  //          - sessionStorage :          

  //      3) 
  //          - localStorage   :   (ex :  ,  )
  //          - sessionStorage :   (ex :  )

  //      4)  
  //          - localStorage   : () 5MB
  //          - sessionStorage : 20MB

  //   # localStorage & sessionStorage 
  //     - localStorage & sessionStorage.setItem(key, value)
  //        :  localStorage  key, value   localStorage  

  //     - localStorage & sessionStorage.getItem(key)
  //        :  localStorage   key , value   localStorage  

  //     - localStorage & sessionStorage.removeItem(key)
  //        :  localStorage   key  key, value   localStorage  


  // JSON  
  //  : JS JavaScript Object Notation(JSON)   JSON    

  //   # JavaScript Object Notation(JSON)
  //      : Javascript       ..    JS object      
  //         -> JSON 
  //            1) JSON    (=  O ,  X)
  //            2)   ,  ("")     
  //            3) JSON '' JSON   ,  (paring) JS Object    
  //               (= JSON     JS object  . or [''];    )

  //   # JSON 
  //     - JSON.stringify(array/object )
  //        : paramter array/object -> JSON      JSON   
  //           ->     ,      

  //     - JSON.parse(JSON  )
  //        : paramter JSON   JSON  , JSON -> array/object   
  //           ->       
  //               -> BUT! object   


  // () localStorage key, value    parameter
  //   -> JSON.stringify()  array/object -> '  ' 
  //       -> JSON.stringify()  , [Object object]  value 
  localStorage.setItem('data', JSON.stringify(obj) );

  // () localStorage JSON   data key value log 
  //   -> JSON       "" 
  //       -> JSON.parse(JSON  )  , JS   ,  
  console.log(JSON.parse(localStorage.getItem('data') ).name );

  // (localStorage )    UI  ,   id localStorage   array  
  //   ->   ,     watchHistory key   JSON  
  useEffect(()=>{

    localStorage.getItem('watchHistory') == null ?
    localStorage.setItem('watchHistory', JSON.stringify( [] )) : null;

  },[]) 

    // (   ) state shoes data.js     object    data  
    let [history, setHistory] = useState( JSON.parse( localStorage.getItem('watchHistory') ) );

  // (tanstack) react-query
  //   : React Application Hooks(use       )   , component    , ,       
  //      -> ,   ajax        ( SNS, )        ...
  //         (= redux  toolkit RTK Query   )
  //             -> , Redux state   ajax     useSlice()   state    , Redux state  ajax      

  //    # react-query 
  //      1) import { QueryClient, QueryClientProvider, useQuery } from '@tanstack/react-query'   (=   index.js) 
  //      2) QueryClient();   QueryClient  ,   <QueryClientProvider clinet={QueryClient()  }>   .. 
  //          -> <QueryClientProvider>   react-query     state    
  //      3) useQuery() hook ,    API    ,   ..?   component  state       ,   HTML     
  
  //          ex) let  = useQuery(' ', () => 
  //                fetch('API url') or axios.get('API url')
  //                .then( (parameter) => {  

  //                  return parameter.data;    ->  result.  ajax      state     
  //                }), 
  //                { 1 : 1 , ... , n : n }
  //              );

  //    # react-query 
  //      1) () state ,   ajax  //  ajax       
  //          - result.isLoading :  ajax    true/false 
  //          - result.error     :  ajax    true/false 
  //          - result.data      :  ajax ,  ,  state   

  //      2) 1)  , ajax    HTML           
  //      3)  component props    ,      
  //          ->   component  ajax  ,    1  ,      

  //      4)  ajax    
  //          ->   { staleTime : /mms } 

  //      5)     
  //          ->     

  // () useQuery()   ajax   result     ,      (= state  )
  let result = useQuery([''], () => 
    fetch('https://codingapple1.github.io/userdata.json')
    .then( (response) => {  

      // refetch( ) 
      console.log('');
      return response.json();
    })

    // axios.get('https://codingapple1.github.io/userdata.json')
    // .then((a)=>{ return a.data })
    , { staleTime : 2000 }
  );

  // result.data;
  // result.isLoading;
  // result.error;


  return (
    <div className="App">

      <Navbar bg="primary" data-bs-theme="dark">
        <Container>
          <Navbar.Brand href="#home">LSH's shop!</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={ () => navigate('/') }>Home</Nav.Link>
            <Nav.Link onClick={ () => navigate('/cart') }>Cart</Nav.Link>
          </Nav>

          {/* () useQuery()  ajax    ,      */}
          <Nav className="ms-auto">
            { result.isLoading && ' ' }
            { result.error && '' }
            { result.data && result.data.name }
          </Nav>

          {/* () public   src     , react {process.env.PUBLIC_URL}          */}
          <img src={process.env.PUBLIC_URL + '/logo192.png'} width="50vh" />
        </Container>
      </Navbar>

      <WatchedHistoryContainer shoes={shoes} history={history}></WatchedHistoryContainer>

      {/* () react-router-dom a     Link component , to  url   Route component component rendering     */}
      {/* <Link to = "/"></Link>
      <Link to = "/detail"></Link> */}

      {/* () (react   2) <Suspense>  
            : fallback   HTML  ,       */}
      <Suspense fallback={ <div></div> }>

        {/* ()  container  Routes component ,  component  url   component rendering       Route component    , 
                    item  Route component ,  url  path   ,   Route component element  rendering  */}
        <Routes>
          <Route path = "/" element = { 
            <>
              {/* <div className="main-bg" style = {{ backgroundImage : 'url('+ img + ')'}} > */}
              <div className="main-bg"></div>
              <Container>
                <Row xs='3'>
                {
                  // () object  data    state  shoes  state  ,   component   
                  shoes.map(function(a, i){
                    return(
                      <Shoes shoes={shoes[i]} key={i}></Shoes>
                    )
                  })
                }
                </Row>
              </Container>

              {/* () axios    axios    get  ,  http   get  url      */}
              <button onClick={ () => {

                //      url json 
                setMoreview(moreView + 1);

                // () axios.get('url')    <->      axios.post('url', {name : 'kim'})   <- post   ,     response 
                //   :  url get  axios  
                //      -> .then( () => {} )
                //           :       
                //              -> ''   ,    (json html) ...
                //                  -> (!) json   axios json -> object array    ..
                //                      ->    ,     ... 
                //                         '.data'   object array   ,  array   
                //
                //      -> .catch( () => {} )
                //           :    
                //
                //      -> finally( () => {})
                //           : ajax /    
                axios.get('https://codingapple1.github.io/shop/data' + moreView + '.json')
                .then((allResponse) => {
                  console.log(allResponse.data);
                  // ()    extraShoes state  setState 
                  setExtraShoes(allResponse.data);
                  
                  let copy = [...shoes, ...allResponse.data];
                  setShoes(copy);
                })
                .catch(() => {
                  console.log(' ...');
                })
              }}></button>

            </>
          }/>

          {/* () 'id' url (:id ) ,  url  id  rendering  component       Route component
                ->   id    element   Detail  useParam     */}
          <Route path = "/detail/:id" element = { 
                                                  // () <context.Provider value={{ state1, ... , stateN}} >
                                                  //   :  context    component ,     state  value     
                                                  //      ->  Detail   context1 context   
                                                  <Context1.Provider value={{ stock, shoes, history, setHistory }}>
                                                    {/* (   ) ()       , history state props           */}
                                                    <Detail shoes = {shoes} history={history} setHistory={setHistory}/> 
                                                  </Context1.Provider>
                                                } />
          

          {/* () Nested Route( )  
                :  url  ,       tree       
                    -> (!) Nested Route ,  Route   <Outlet></Outlet>   Route  rendering     */}
          <Route path = "/about" element = { <About /> } >
            <Route path = "member" element = { <div></div> } />
            <Route path = "location" element = { <div></div> } />
          </Route>

          {/* () Nested Route     Route
          <Route path = "/about" element = { <About /> } />
          <Route path = "/about/member" element = { <div></div> } />
          <Route path = "/about/location" element = { <div></div> } />  */}

          {/* () /event/one /event/two      */}
          <Route path = "/event" element = { <div>
                                              <h3> </h3>
                                              <Outlet></Outlet>
                                            </div> } >
            <Route path = "one" element = { <div>   </div> } />
            <Route path = "two" element = { <div></div> } />
          </Route>

          <Route path="/cart" element={<Cart/>}>

          </Route>


          {/* () path  *  =   url   url   element rendering   */}
          <Route path = "*" element = { <div>404!   </div> } />
        </Routes>
      </Suspense>


    </div>
  );
}

// ()    +  map  +     binding  
//   ->     ,   app   
//      (= props      )
function Shoes(props){

  // useNavigate 
  //  : Link component ,   react hooks  react router  react hooks  
  //     - useNavigate('url')
  //        : <Link to = "url" />  

  //     - useNavigate(+ or -)
  //        : +    , -     
  let navigate = useNavigate();

  return (
    <>
      {/* ()   ,    */}
      <Col onClick={ () => navigate('/detail/' + props.shoes.id ) }>
        <img src={'https://codingapple1.github.io/shop/shoes' + (props.shoes.id + 1) + '.jpg'} width="80%" />
        <h4>{props.shoes.title}</h4>
        <p>{props.shoes.content}</p>
      </Col>
    </>
  );

}

function About(){
  return(
    <div>
      <h4></h4>
      {/* () Routes  component  url  url  Nested Routes       */}
      <Outlet></Outlet>
    </div>
  )
}

// (   ) localStrage        id ,     
// ()      container    WatchedHistoryContainer
function WatchedHistoryContainer(props){

  // ()    container    , historyContainer class  css style  
  return(

    <div className='historyContainer'>
      <div>
        <b>  </b>
      </div>
      {
        // () props    history null    
        props.history == null ? null :

        // () watchHistory      id ,  WatchedHistory  id   image  item   
        props.history.map(function(a, j){

          // ()    index localStrage  watchHistory    json  
          const propsId = Number(props.history[j]);

          //     item  WatchedHistoryItem      props propsId 
          return(
            <WatchedHistoryItem propsId={propsId} key={j} ></WatchedHistoryItem>
          )
        })

      }
      <div>
      </div>
    </div>
  )

}

// (   ) ()      item    WatchedHistory
function WatchedHistoryItem(props){

  // url     useNavigate()  

  // useNavigate 
  //  : Link component ,   react hooks  react router  react hooks  
  //     - useNavigate('url')
  //        : <Link to = "url" />  

  //     - useNavigate(+ or -)
  //        : +    , -     
  let navigate = useNavigate();

  return(
    <>
      {/* ()   ,    
            ->  navigate   url      props history state set , localstrage watchHistory  ,  state     */}
      <div className='eachHistoryItem' onClick={ () => { navigate( '/detail/' + props.propsId ) } }>
        <h6></h6>
        <img src={'https://codingapple1.github.io/shop/shoes' + (props.propsId + 1) + '.jpg'} width="80%" height="80%"/>
      </div>
    </>
  )
}


export default App;