// () REDUX 
//   : props        , context    component state     

//   # redux 
//      :   state  store js   ... REDUX   ,  component   store  state  

//   # react-redux 
//      : (node js   + react 18  )     npm install @reduxjs/toolkit react-redux  
//          -> redux toolkit =  redux 

//   # redux ()
//     1) redux store       state 
//         -> (!)   state  redux  , component  state {}        

//     2) store.js state   ..     state export ..    !
//        (= camalCase  , react     ... )

//         ex) cartData = cartData.reduce (x)  -> cartdata = cartData.reduce (o)

//   # react-redux 
//     1) (  X)   state   store.js ,      
//         ->  js redux    component    

//     2) redux  , store.js state   component  js  Provider       import 
//        (=    component    , index.js import )

//         ex) import store from './store.js'

//     3) store.js  state        component Provider  , store  store.js import    
//         ->  Provider   react-redux ,  import   

//         ex) import { Provider } from 'react-redux';

//             <Provider store={import}> <> </Provider>

//     4) store.js  createSlice({name: state, initialState: state});   state 
//         -> () initialState   state     or      
//         -> () , setState    , reducers  ,     +   export state.action;  

//         ex) let user = createSlice({
//                 name : 'state',
//                 initialState : state or [state1, ... ,  stateN] or {1 : 1, ... , {1 : 1} 

//                 reducers : {
//                     1(state){ return 'john' + state; },
//                     2(state){ return { name : 'park', age : 20 }; },
//                     3(state){ state.name = 'park'; },
//                     .... , 
//                     N(state, action){ state.age += action.payload; }

//                 }
//             })

//             export let { 1, ... ,N } = cartData.actions;

//     5)    parameter  configureStore( { reducer: {} } ) , reducer       store.js state   
//         ->   : state1 : state1.reducer 

//         ex) export default configureStore({
//                reducer: {
//                   state1 : state1.reducer , 
//                   ... , 
//                   stateN : stateN.reducer 
//                }
//             }) 

//     6)   state   component  useSelector(), useDispatch   , useSelector redux  import

//         ex) import { useSelector, useDispatch } from 'react-redux';

//     7) store.js  state ,    state  
//         -> useSelector( (state  parameter) => { return parameter } )  

//     8) store.js  state     
//         -> import      store.js  
//         -> useDispatch(import ());  ,   store.js     

//         ex) import { 1, ... , N } from "./../store";

//             useDispatch(changeName());

//     9)    component  return 


//  # redux  
//     1) createSlice({name: state, initialState: state or [state1, ... ,  stateN] or {1 : 1, ... , {1 : 1} });
//         : store.js state  .. parameter state    object 

//           @ createSlice() parameter  
//              - name         : state    (   reducer   )
//              - initialState : state    
//                                 -> () initialState   state     or      
//              - reducer      : state    createSlice() parameter 
//                                 -> () state    'state.action'  state  export 
//                                 ->  state    

//     2) useSelector( (parameter) => { return parameter } )
//        : redux     store ,    state  state    react hooks   (= useContext() )
//          (= axios fetch   API      , return  parameter.state     )
//             -> () tools  react hooks     API   

//     3) configureStore( { reducer : { state1 : state1.reducer , ... , stateN : stateN.reducer })
//         : store.js  state       
//             -> ()      state   ... 
//             -> ()  state  state  'state.reducer' 
//             -> () state reducer , state  configureStore()  , 'state.action'  state  export 

//     4) useDispatch(import  state)
//         : store.js    state , store.js   redux  react hooks  
//            ->  ..    store.js 

//     5) current(state)
//         : proxy  state      


// #  state   
//    : store.js    state  createSlice()  parameter    name, initialState  reduce      (=  setState   )
//       -> ()   parameter 2 
//            1. state
//                :  state     parameter this   
//                   -> state   proxy       current(state)    

//            2. action
//                :        parameter.. 
//                  (=  state  array/object      ,           parameter  )
//                      ->   , payload      + type     

//      ex) reducers : {

//          1)  state     
//              :   state      state  , state parameter   state   state  

//                changeNameExample1(state){
//                    return 'john' + state.name;
//                },

//          2)  state     
//              :   state      state     

//                changeNameExample2(){
//                    return { name : 'park', age : 20 };
//                },

//          3)  state   '(array) or (object)' 
//              : return    state  state parameter   ,  state    
//                 -> immer.js  redux   , return  array/object  
//                    (= immer.js    state   array/object    ,      return )

//                changeNameExample3(state){
//                    state.name = 'park';
//                },

//          4)  state    parameter  ,      ( or index)    
//              : this  state 1 parameter,          action 2 parameter  payload      or index    
//                 ->    parameter     js  ,      

//                increaseAge(state, action ){
//                    state.age += action.payload;        <- action    = increaseAge(100) : state 100  
//                }, 

//                setCount(state, action){
//                    state[action.payload].count++;      <- action index  = setCount(100) : 101  count  1 
//                }
// }


//  # () useDispatch   state    ?
//     1)  component  state    ,  state    store.js  ,    
//     2)   state  ,  store.js   ..      



// () redux     
//   -> redux toolkit =  redux    module import
import { configureStore, createSlice, current } from '@reduxjs/toolkit'

// ()    store.js import
import user from './storeModule/userSlice.js'


// () createSlice   stock state  (  )
let stock = createSlice({
    name : 'stock',
    initialState : [10, 11, 12]
})

// (1)   Redux store   ->  Cart.js     

// (1 ) createSlice   cartdata state  (   )
let cartData = createSlice({
    name : 'cartData',
    initialState : [
                        {id : 0, name : 'White and Black', count : 2},
                        {id : 2, name : 'Grey Yordan', count : 1}
                    ],
    // () reducers    state    (=  setState   )
    //   ->   parameter   state  
    reducers : {
        setCount(state, action){

            // (2) + ,    count +1   

            // (2 ) object      index  findIndex ,
            //   ->  id action ..  id   index ? 
            //       -> this  state [index]       
            const index = state.findIndex( (state) =>  state.id == action.payload );
            state[index].count++;
        },

        // (3)   ,        
        //   ->  :   component  state  ,  state   (= store.js    redux )
        addCart(state, action){

            // (3 )
            //   : onClick cart        action ,  state parameter      state  push()  
            // state.push(action.payload);

            // (3  )  
            //   : onClick findShoes state  action parameter , state parameter      state  action.payload    push()  
            console.log(current(state));

            const object = {id : action.payload.id, name : action.payload.title, count : 1};
            state.push(object);

            console.log(current(state));
        }
    } 
})

// () cartData state  setCount     export
export let { setCount, addCart } = cartData.actions;

// () stock.js state    state user, stock, cartdata state   
export default configureStore({

    // ()  state  , reducer   
    //   -> 'export  state' :  state.reducer  
    reducer: {
        user : user.reducer , 
        stock : stock.reducer ,
        // (1 ) ()  export  state    ! 
        // cartData : cartData.reducer
        cartdata : cartData.reducer
    }
}) 