// () store.js       userSlice    
//   ->    import   store.js  
import { createSlice } from "@reduxjs/toolkit";

// () createSlice   user state  (  )
let user = createSlice({
    name : 'user' ,
    initialState : { name : 'kim', age : 20 },

    // () reducers    state    (=  setState   )
    //   ->   parameter   state  (= this   )
    reducers : {

        // (1)   state    state   ,   
        changeNameExample1(state){
            return 'john' + state.name;
        },

        // (2)   state    state  ,      
        changeNameExample2(){
            return { name : 'park', age : 20 };
        },

        // (3) state  '(array) or (object)' , return     state     
        //   -> return  immer.js  redux   ,     state       ,   state return  
        changeNameExample3(state){
            state.name = 'park';
        },

        // () , user  age parameter      
        increaseAge(state, action ){
            state.age += action.payload;
        }
    }

})
// increaseAge(10);
// increaseAge(Math.floor(Math.random * 10));

// () user state  changeNameExample     export
export let { changeNameExample1, changeNameExample2, changeNameExample3, increaseAge} = user.actions;
export let changeName = user.actions.changeNameExample3;

export default user;