import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';

//  PWA ,         service-worker.js     import  
import * as ServiceWorkerRegistration from './serviceWorkerRegistration';

// reportWebVitals
//  : CRA(create-react-app)      ,    index.js ,         
//    (,     ...    )
//      -> reportWebVitals(console.log)        

// CRA(create-react-app)     reportWebVitals  import  
import reportWebVitals from './reportWebVitals';

// REDUX  state  store.js  import  
import store from './store.js';

// react  routing   , BrowserRouter component react-router-dom   import  
import { BrowserRouter } from 'react-router-dom';

// redux  store.js  state  component    provider component    redux  
import { Provider } from 'react-redux';

// react  ajax           react-query   import  
import { QueryClient, QueryClientProvider, useQuery } from '@tanstack/react-query' 

// () react-query   <QueryClientProvider>   client   queryClient  ,     react-query      
const queryClient = new QueryClient();

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  // React.StrictMode
  //  : react    ..   log 2  ,   
  <React.StrictMode>
    {/* () react-query      component    */}
    <QueryClientProvider client={queryClient}>
      {/* () App   store.js  state redux      Provider  store   */}
      <Provider store={store} >
        <BrowserRouter basename={process.env.PUBLIC_URL}>
          <App />
        </BrowserRouter>
      </Provider>
    </QueryClientProvider>
  </React.StrictMode>
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA

//    ServiceWorkerRegistration       ServiceWorkerRegistration  
//  -> npx create-react-app  --template cra-template-pwa    ServiceWorkerRegistration.unregister();
ServiceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
